export default {
	data() {
		return {
			login: false,
			avatarUrl: '/static/logo.png',
			uerInfo: {}
		}
	},
	methods: {
		goLogin() {
			uni.navigateTo({
				url: '/pages/login/login'
			})
		},
		checkSession(){
			uni.checkSession({
				success: (res) => {
					this.login = true
					this.uerInfo = uni.getStorageSync('userInfo')
				},
				fail: () =>{
					this.login = false
					
				}
			})
		}
	},
	onShow(){
		this.checkSession()
	}
}
